import React from 'react';
import _ from 'lodash';
import '../assets/styles/pagination.css';
// import '../assets/styles/_pagination.scss';

const Pagination = (props) => {
    const {itemsCount, pageSize, currentPage, onPageChange} = props; //ê°ê° ì•„ì´í…œ(ë°©ëª…ë¡)ê°œìˆ˜, í•œ í˜ì´ì§€ì— ë³´ì—¬ì¤„ ì•„ì´í…œ
    const pageCount = Math.ceil(itemsCount / pageSize); //ëª‡ í˜ì´ì§€ê°€ í•„ìš”í•œì§€ ê³„ì‚°
    if(pageCount === 1) return null; //1í˜ì´ì§€ ë¿ì´ë¼ë©´ í˜ì´ì§€ ìˆ˜ë¥¼ ë³´ì—¬ì£¼ì§€ ì•ŠìŒ

    const pages = _.range(1, pageCount + 1); //ë§ˆì§€ë§‰ í˜ì´ì§€ì— ë³´ì—¬ì¤„ ì»¨í…ì¸ ë¥¼ ìœ„í•´ +1, https://lodash.com/docs/#range ì°¸ê³ 

    return(
            <nav arial-label="Page navigation example" className="pagination_style">
                <ul className="pagination">
                    {pages.map(page => (
                        <li key={page}
                        className={page === currentPage ? "page-item active" : "page-item"} //Bootstrapì„ ì´ìš©í•´ì„œ í˜„ì¬ í˜ì´ì§€ë¥¼ ì‹œê°ì ìœ¼ë¡œ í‘œì‹œ
                        style={{cursor: "pointer"}}>
                            <a className="page-link" onClick={() => onPageChange(page)}>{page}</a> {/* í˜ì´ì§€ ë²ˆí˜¸ í´ë¦­ ì´ë²¤íŠ¸ ì²˜ë¦¬ê¸° ì§€ì • */}
                        </li>
                    ))}
                </ul>
            </nav>
    )
}

export default Pagination;